import React, { useCallback } from 'react'
import styled from 'styled-components'
import BigNumber from 'bignumber.js'
import { Button, Flex, Heading, IconButton, AddIcon, MinusIcon, useModal } from 'uikit'
import { useLocation } from 'react-router-dom'
import { useTranslation } from 'contexts/Localization'
import useLiquidityStake from 'hooks/useLiquidityStake'
import useLiquidityUnstake from 'hooks/useLiquidityUnstake'
import { getBalanceNumber, getFullDisplayBalance } from 'utils/formatBalance'
import DepositModal from '../DepositModal'
import WithdrawModal from '../WithdrawModal'

interface StakeActionsProps {
  stakedBalance?: BigNumber

  tokenBalance?: BigNumber

  tokenName?: string

  liquidityid?: number

  addLiquidityUrl?: string
}

const IconButtonWrapper = styled.div`
  display: flex;
  svg {
    width: 20px;
  }
`

const StakeAction: React.FC<StakeActionsProps> = ({
  stakedBalance,
  tokenBalance,
  tokenName,
  liquidityid,
  addLiquidityUrl,
}) => {
  const { t } = useTranslation()

  let stakeCallbackHander

  const stakeModalCallbackSetting = ((callback)=>
  {
    stakeCallbackHander = callback
  })

  const stakeCallback = () => 
  {
    if(stakeCallbackHander)
    {
      stakeCallbackHander()
    }
  }

  const [ onStake ] = useLiquidityStake(liquidityid, stakeCallback)

  let unstakeCallbackHander

  const unstakeModalCallbackSetting = ((callback)=>
  {
    unstakeCallbackHander = callback
  })


  const unstakeCallback = () =>
  {
    if(unstakeCallbackHander)
    {
      unstakeCallbackHander()
    }
  }

  const [ onUnstake ] = useLiquidityUnstake(liquidityid, false, unstakeCallback)

  const location = useLocation()

  const displayBalance = useCallback(() => {
    const stakedBalanceNumber = getBalanceNumber(stakedBalance)

    if (stakedBalanceNumber > 0 && stakedBalanceNumber < 0.0001) {
      return getFullDisplayBalance(stakedBalance).toLocaleString()
    }
    
    return stakedBalanceNumber.toLocaleString()
  }, [stakedBalance])

  const [onPresentDeposit] = useModal(
    <DepositModal max={tokenBalance} onConfirm={onStake} decimals={18} setCallback={stakeModalCallbackSetting} tokenName={tokenName} addLiquidityUrl={addLiquidityUrl} />,
  )
  const [onPresentWithdraw] = useModal(
    <WithdrawModal max={stakedBalance} onConfirm={onUnstake} decimals={18} tokenName={tokenName} setCallback={unstakeModalCallbackSetting}/>,
  )

  const renderStakingButtons = () => {
    return stakedBalance.eq(0) ? (
      <Button
        onClick={onPresentDeposit}
        disabled={['history', 'archived'].some((item) => location.pathname.includes(item))}
      >
        {t('Stake LP')}
      </Button>
    ) : (
      <IconButtonWrapper>
        <IconButton variant="tertiary" onClick={onPresentWithdraw} mr="6px">
          <MinusIcon color="primary" width="14px" />
        </IconButton>
        <IconButton
          variant="tertiary"
          onClick={onPresentDeposit}
          disabled={['history', 'archived'].some((item) => location.pathname.includes(item))}
        >
          <AddIcon color="primary" width="14px" />
        </IconButton>
      </IconButtonWrapper>
    )
  }

  return (
    <Flex justifyContent="space-between" alignItems="center">
      <Heading color={stakedBalance.eq(0) ? 'textDisabled' : 'text'}>{displayBalance()}</Heading>
      {renderStakingButtons()}
    </Flex>
  )
}

export default StakeAction
