import { getENVChainId, ADDRESS0, DEFAULT_GAS_LIMIT } from 'sdk'
import { splitSignature } from '@ethersproject/bytes'
import getAirdrops from 'sdk/airdrops'
import { useCallback } from 'react'
import { useAppDispatch } from 'state'
import { useWeb3React } from '@web3-react/core'
import { useUserDeadline } from 'state/user/hooks'
import { fetchAirdropsUserDataAsync, fetchAirdropsDataAsync } from 'state/actions'
import { useAirdropContract } from './useContract'

export default function useAirdropReward(
  id, callback: any, _invitor?: string
): [ () => Promise<void>] {
  const { account, library } = useWeb3React()

  const dispatch = useAppDispatch()

  const chainId = getENVChainId()

  const airdropContract = useAirdropContract(id)

  if(!_invitor)
  {
    _invitor = ADDRESS0
  }

  const [deadline] = useUserDeadline()

  const deadlineForSignature: number = Math.ceil(Date.now() / 1000) + deadline

  const onReward = useCallback(async (): Promise<void> => {
    if (!airdropContract) {
      console.error('airdrop contract is null')

      return
    }

    const airdrop = getAirdrops().find((p) => p.id === id)

    const nonce = await airdropContract.nonces(account)

    const EIP712Domain = [
      { name: 'version', type: 'uint256' },
      { name: 'chainId', type: 'uint256' },
      { name: 'verifyingContract', type: 'address' }
    ]

    const domain = {
      version: airdrop.version,
      chainId,
      verifyingContract: airdrop.address
    }

    const reward = [
      { name: 'invitor', type: 'address' },
      { name: 'nonce', type: 'uint256' },
      { name: 'deadline', type: 'uint256' }
    ]

    const message = {
      invitor: _invitor,
      nonce: nonce.toHexString(),
      deadline: deadlineForSignature,
    }

    const data = JSON.stringify({
      types: {
        EIP712Domain,
        reward,
      },
      domain,
      primaryType: 'reward',
      message,
    })

    library
      .send('eth_signTypedData_v4', [account, data])
      .then(splitSignature)
      .then((signature) => {
         const v = signature.v
         const r = signature.r
         const s = signature.s
         airdropContract
             .reward(_invitor, nonce.toHexString(), deadlineForSignature, v, r, s, { gasLimit: DEFAULT_GAS_LIMIT })
             .then(() => {
                      dispatch(fetchAirdropsUserDataAsync(account))
                      dispatch(fetchAirdropsDataAsync()) 
                               callback()
                              })
                                       .catch((error: Error) => {
                                         console.error('Failed to harvest token', error)
                                         throw error
                                 })
                                })
                           
  }, [airdropContract, dispatch, id, account, _invitor, callback, library, deadlineForSignature, chainId])

  return [ onReward ]
}

 
