import React from 'react'
import styled from 'styled-components'
import {
  ButtonMenu,
  ButtonMenuItem,
  Flex,
  NotificationDot,
} from 'uikit'
import { useTranslation } from 'contexts/Localization'

const LiquiditypoolTab = ({ showHistory, setShowHistory, hasStakeInFinishedFarms }) => {
  const { t } = useTranslation()

  return (
    <Wrapper>
      <Flex alignItems="center" flexDirection={['column', null, 'row', null]}>
        <ButtonMenu activeIndex={showHistory ? 1 : 0} onItemClick={(index) => setShowHistory(index === 1)} scale="sm" variant="subtle">
          <ButtonMenuItem>
            {t('Live')}
          </ButtonMenuItem>
          <NotificationDot show={hasStakeInFinishedFarms}>
            <ButtonMenuItem>
              {t('Finished')}
            </ButtonMenuItem>
          </NotificationDot>
        </ButtonMenu>
      </Flex>
    </Wrapper>
  )
}

export default LiquiditypoolTab

const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;

  a {
    padding-left: 12px;
    padding-right: 12px;
  }

  ${({ theme }) => theme.mediaQueries.sm} {
    margin-left: 16px;
  }
`
