import { BIG_TEN } from 'sdk'
import { useCallback } from 'react'
import { useAppDispatch } from 'state'
import BigNumber from 'bignumber.js'
import { useWeb3React } from '@web3-react/core'
import { fetchFarmUserDataAsync } from 'state/actions'
import { useLiquidityPoolContract } from './useContract'
import { calculateGasMargin } from '../utils'

export default function useLiquidityStake(
  liquidityId, callback: any
): [ (amount: string, decimals: number) => Promise<void>] {
  const { account } = useWeb3React()

  const dispatch = useAppDispatch()

  const liquidityPoolContract = useLiquidityPoolContract(liquidityId)
  
  const onStake = useCallback(async (amount: string, decimals = 18): Promise<void> => {
    if (!liquidityPoolContract) {
      console.error('liquidity pool contract is null')

      return
    }

    const estimatedGas = await liquidityPoolContract.estimateGas.deposit(new BigNumber(amount).times(BIG_TEN.pow(decimals)).toString())
                                                      
    // eslint-disable-next-line consistent-return
    liquidityPoolContract
           .deposit((new BigNumber(amount).times(BIG_TEN.pow(decimals)).toString()), { gasLimit: calculateGasMargin(estimatedGas) })
           .then(() => {
              dispatch(fetchFarmUserDataAsync(account))

              callback()
      })
      .catch((error: Error) => {
        console.error('Failed to stake token', error)
        throw error
      })
    
  }, [liquidityPoolContract, dispatch, account, callback])

  return [ onStake ]
}