import React, { useState, useCallback } from 'react'
import styled from 'styled-components'
import { provider as ProviderType } from 'web3-core'
import BigNumber from 'bignumber.js'
import { Button, Flex, Text } from 'uikit'
import { LiquidityPool } from 'state/types'
import { useTranslation } from 'contexts/Localization'
import useToast from 'hooks/useToast'
import { ApprovalState } from 'hooks/useApproveCallback'
import useLiquidityApprove from 'hooks/useLiquidityApprove'
import UnlockButton from 'components/UnlockButton'
import StakeAction from './StakeAction'
import HarvestAction from './HarvestAction'

const Action = styled.div`
  padding-top: 16px;
`
export interface LiquidityPoolWithStakedValue extends LiquidityPool {
  apr?: number
}

interface LiquidityPoolCardActionsProps {
  liquidity: LiquidityPoolWithStakedValue

  provider?: ProviderType

  account?: string

  addLiquidityUrl?: string
}

const LiquidityPoolCardActions: React.FC<LiquidityPoolCardActionsProps> = ({ liquidity, account, addLiquidityUrl }) => {
  const { t } = useTranslation()

  const [requestedApproval, setRequestedApproval] = useState(false)

  const {
    allowance: allowanceAsString = 0,

    tokenBalance: tokenBalanceAsString = 0,

    stakedBalance: stakedBalanceAsString = 0,

    earnings: earningsAsString = 0,

  } = liquidity.userData || {}

  const allowance = new BigNumber(allowanceAsString)

  const tokenBalance = new BigNumber(tokenBalanceAsString)

  const stakedBalance = new BigNumber(stakedBalanceAsString)

  const earnings = new BigNumber(earningsAsString)

  const lpName = liquidity.liquidity.symbol.toUpperCase() 

  const isApproved = account && allowance && allowance.isGreaterThan(0)

  const { toastSuccess, toastError } = useToast()

  const approveCallback = () =>
  {
    toastSuccess(`${t('Contract Enabled')}`, `${t(`You can now stake in the farm!`)}`)
       
    setRequestedApproval(false)
  }

  const [ approveState, onApprove ] = useLiquidityApprove(liquidity.liquidity, liquidity.liquidityid, approveCallback)

  const handleApprove = useCallback(async () => {
    try {
      setRequestedApproval(true)

      await onApprove()

      if (approveState ===  ApprovalState.UNKNOWN) {
        // user rejected tx or didn't go thru
        toastError(
          `${t('Error')}`,
          `${t(`Please try again. Confirm the transaction and make sure you are paying enough gas!`)}`,
        )

        setRequestedApproval(false)
      }  else if (approveState === ApprovalState.APPROVED)
      {
        toastSuccess(`${t('Contract Enabled')}`, `${t(`You can now stake in the farm!`)}`)
       
        setRequestedApproval(false)
      }

    } catch (e) {
      console.error(e)
    }
  }, [onApprove, approveState, toastSuccess, toastError, t])

  const renderApprovalOrStakeButton = () => {
    return isApproved ? (
      <StakeAction
        stakedBalance={stakedBalance}
        tokenBalance={tokenBalance}
        tokenName={lpName}
        liquidityid={liquidity.liquidityid}
        addLiquidityUrl={addLiquidityUrl}
      />
    ) : (
      <Button mt="8px" width="100%" disabled={requestedApproval} onClick={handleApprove}>
        {t('Approve Contract')}
      </Button>
    )
  }

  return (
    <Action>
      <Flex>
        <Text bold textTransform="uppercase" color="secondary" fontSize="12px" pr="3px">
          {/* TODO: Is there a way to get a dynamic value here from useFarmFromSymbol? */}
          {liquidity.earningToken.symbol.toUpperCase()}
        </Text>
        <Text bold textTransform="uppercase" color="textSubtle" fontSize="12px">
          {t('Earned')}
        </Text>
      </Flex>
      <HarvestAction earnings={earnings} earningToken={liquidity.earningToken} liquidityid={liquidity.liquidityid} />
      <Flex>
        <Text bold textTransform="uppercase" color="secondary" fontSize="12px" pr="3px">
          {lpName}
        </Text>
        <Text bold textTransform="uppercase" color="textSubtle" fontSize="12px">
          {t('Staked')}
        </Text>
      </Flex>
      {!account ? <UnlockButton mt="8px" width="100%" /> : renderApprovalOrStakeButton()}
    </Action>
  )
}

export default LiquidityPoolCardActions
