import React, { useEffect, useCallback, useState, useMemo, useRef } from 'react'
import { useAppDispatch } from 'state'
import BigNumber from 'bignumber.js'
import { useWeb3React } from '@web3-react/core'
import { Heading, RowType, Toggle, Text } from 'uikit'
import styled from 'styled-components'
import FlexLayout from 'components/layout/Flex'
import Page from 'components/layout/Page'
import { useLiquidityPools, useGetApiPrices, useBlock } from 'state/hooks'
import useRefresh from 'hooks/useRefresh'
import { fetchFarmUserDataAsync } from 'state/actions'
import usePersistState from 'hooks/usePersistState'
import { LiquidityPool } from 'state/types'
import { useTranslation } from 'contexts/Localization'
import { getBalanceNumber } from 'utils/formatBalance'
import { orderBy } from 'lodash'
import { latinise } from 'utils/latinise'
import FarmsPageHeader from 'components/FarmsPageHeader'
import { fetchFarmsPublicDataAsync } from 'state/liquiditypools'
import Select, { OptionProps } from 'components/Select/Select'
import Liquiditypoolcard from './components/Liquiditypoolcard/Liquiditypoolcard'
import { LiquidityPoolWithStakedValue } from './components/Liquiditypoolcard/types'
import Table from './components/Liquiditypooltable/FarmTable'
import LiquiditypoolTab from './LiquiditypoolTab'
import SearchInput from './components/SearchInput'
import { RowProps } from './components/Liquiditypooltable/Row'
import ToggleView from './components/ToggleView'
import { DesktopColumnSchema, ViewMode } from './components/types'

const ControlContainer = styled.div`
  display: flex;
  width: 100%;
  align-items: center;
  position: relative;

  justify-content: space-between;
  flex-direction: column;
  margin-bottom: 32px;

  ${({ theme }) => theme.mediaQueries.sm} {
    flex-direction: row;
    flex-wrap: wrap;
    padding: 16px 32px;
    margin-bottom: 0;
  }
`

const ToggleWrapper = styled.div`
  display: flex;
  align-items: center;
  margin-left: 10px;

  ${Text} {
    margin-left: 8px;
  }
`

const LabelWrapper = styled.div`
  > ${Text} {
    font-size: 12px;
  }
`

const FilterContainer = styled.div`
  display: flex;
  align-items: center;
  width: 100%;
  padding: 8px 0px;

  ${({ theme }) => theme.mediaQueries.sm} {
    width: auto;
    padding: 0;
  }
`

const ViewControls = styled.div`
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
  align-items: center;
  width: 100%;

  > div {
    padding: 8px 0px;
  }

  ${({ theme }) => theme.mediaQueries.sm} {
    justify-content: flex-start;
    width: auto;

    > div {
      padding: 0;
    }
  }
`
const NUMBER_OF_FARMS_VISIBLE = 12

const Liquiditypools: React.FC = () => {
  const { t } = useTranslation()

  const { data: farmsLP, userDataLoaded } = useLiquidityPools()

  const [query, setQuery] = useState('')

  const [viewMode, setViewMode] = usePersistState(ViewMode.TABLE, 'grampus_farm_view')

  const [stakedOnly, setStakedOnly] = usePersistState(false, 'grampus_farm_pool_staked')

  const [showHistory, setShowHistory] = usePersistState(true, 'grampus_farm_pool_history')

  const { account } = useWeb3React()

  const [sortOption, setSortOption] = useState('hot')

  const prices = useGetApiPrices()

  const dispatch = useAppDispatch()

  const { fastRefresh } = useRefresh()

  useEffect(() => {
    if (account) {
      dispatch(fetchFarmUserDataAsync(account))
    }
  }, [account, dispatch, fastRefresh])

  // Users with no wallet connected should see 0 as Earned amount
  // Connected users should see loading indicator until first userData has loaded
  const userDataReady = !account || (!!account && userDataLoaded)

  useEffect(() => {
    dispatch(fetchFarmsPublicDataAsync())

    if (account) {
     dispatch(fetchFarmUserDataAsync(account))  
    }
  }, [dispatch, account])

  const { currentBlock } = useBlock()

  const inactiveFarms = farmsLP.filter((farm) => (currentBlock >= farm.endBlock && farm.endBlock > 0))
  
  const activeFarms = farmsLP.filter((farm) => (currentBlock < farm.endBlock))

  const stakedOnlyFarms = activeFarms.filter(
    (farm) => farm.userData && new BigNumber(farm.userData.stakedBalance).isGreaterThan(0),
  )

  const stakedInactiveFarms = inactiveFarms.filter(
    (farm) => farm.userData && new BigNumber(farm.userData.stakedBalance).isGreaterThan(0),
  )

  const farmsList = useCallback(
    (farmsToDisplay: LiquidityPool[]): LiquidityPoolWithStakedValue[] => {
      let farmsToDisplayWithAPR: LiquidityPoolWithStakedValue[] = farmsToDisplay.map((farm) => {
        if (!farm.lpTotalInQuoteToken || !prices) {
          return farm
        }

        const quoteTokenPriceUsd = prices[farm.liquidity.quoteToken.address.toLowerCase()]

        const totalLiquidity = new BigNumber(farm.lpTotalInQuoteToken).times(quoteTokenPriceUsd)

        const apr = 0

        return { ...farm, apr, liquidityvalue: totalLiquidity }
      })

      if (query) {
        const lowercaseQuery = latinise(query.toLowerCase())

        farmsToDisplayWithAPR = farmsToDisplayWithAPR.filter((farm: LiquidityPoolWithStakedValue) => {
          return latinise(farm.liquidity.symbol.toLowerCase()).includes(lowercaseQuery)
        })
      }
      return farmsToDisplayWithAPR
    },
    [prices, query],
  )

  const handleChangeQuery = (event: React.ChangeEvent<HTMLInputElement>) => {
    setQuery(event.target.value)
  }

  const loadMoreRef = useRef<HTMLDivElement>(null)

  const [numberOfFarmsVisible, setNumberOfFarmsVisible] = useState(NUMBER_OF_FARMS_VISIBLE)

  const [observerIsSet, setObserverIsSet] = useState(false)

  const farmsStakedMemoized = useMemo(() => {
    let farmsStaked = []

    const sortFarms = (farms: LiquidityPoolWithStakedValue[]): LiquidityPoolWithStakedValue[] => {
      switch (sortOption) {
        case 'apr':
          return orderBy(farms, (farm: LiquidityPoolWithStakedValue) => farm.apr, 'desc')
 
        case 'earned':
          return orderBy(
            farms,
            (farm: LiquidityPoolWithStakedValue) => (farm.userData ? Number(farm.userData.earnings) : 0),
            'desc',
          )
        case 'liquidity':
          return orderBy(farms, (farm: LiquidityPoolWithStakedValue) => Number(farm.liquidity), 'desc')
        default:
          return farms
      }
    }

    if (!showHistory) {
      farmsStaked = stakedOnly ? farmsList(stakedOnlyFarms) : farmsList(activeFarms)
    }

    if (showHistory) {
      farmsStaked = stakedOnly ? farmsList(stakedInactiveFarms) : farmsList(inactiveFarms)
    }

    return sortFarms(farmsStaked).slice(0, numberOfFarmsVisible)
  }, [
    sortOption,
    activeFarms,
    farmsList,
    inactiveFarms,
    showHistory,
    stakedInactiveFarms,
    stakedOnly,
    stakedOnlyFarms,
    numberOfFarmsVisible,
  ])

  useEffect(() => {
    const showMoreFarms = (entries) => {
      const [entry] = entries
      if (entry.isIntersecting) {
        setNumberOfFarmsVisible((farmsCurrentlyVisible) => farmsCurrentlyVisible + NUMBER_OF_FARMS_VISIBLE)
      }
    }

    if (!observerIsSet) {
      const loadMoreObserver = new IntersectionObserver(showMoreFarms, {
        rootMargin: '0px',
        threshold: 1,
      })
      loadMoreObserver.observe(loadMoreRef.current)
      setObserverIsSet(true)
    }
  }, [farmsStakedMemoized, observerIsSet])

  const rowData = farmsStakedMemoized.map((farm) => {
    const { token, quoteToken } = farm.liquidity

    const tokenAddress = token.address

    const quoteTokenAddress = quoteToken.address
    
    const lpLabel = farm.liquidity.symbol && farm.liquidity.symbol.split(' ')[0].toUpperCase().replace('PANCAKE', '')

    const row: RowProps = {
      apr: {
        value: farm.apr && farm.apr.toLocaleString('en-US', { maximumFractionDigits: 2 }),
        lpLabel,
        tokenAddress,
        quoteTokenAddress,
        originalValue: farm.apr,
      },

      farm: {
        image: farm.liquidity.symbol.split(' ')[0].toLocaleLowerCase(),
        label: lpLabel,
        liquidityid: farm.liquidityid,
      },

      earned: {
        earnings: getBalanceNumber(new BigNumber(farm.userData.earnings), farm.earningToken.decimals),
        liquidityid: farm.liquidityid,
      },

      liquidity: {
        liquidityvalue: farm.liquidityvalue,
      },

      details: farm,
    }

    return row
  })

  const renderContent = (): JSX.Element => {
    if (viewMode === ViewMode.TABLE && rowData.length) {
      const columnSchema = DesktopColumnSchema

      const columns = columnSchema.map((column) => ({
        id: column.id,
        name: column.name,
        label: column.label,
        sort: (a: RowType<RowProps>, b: RowType<RowProps>) => {
          switch (column.name) {
            case 'farm':
              return b.id - a.id
            case 'apr':
              if (a.original.apr.value && b.original.apr.value) {
                return Number(a.original.apr.value) - Number(b.original.apr.value)
              }

              return 0
            case 'earned':
              return a.original.earned.earnings - b.original.earned.earnings
            default:
              return 1
          }
        },
        sortable: column.sortable,
      }))

      return <Table data={rowData} columns={columns} userDataReady={userDataReady} />
    }

    return (
      <div>
          { !showHistory ? 
                <FlexLayout>
            {farmsStakedMemoized.map((farm) => (
              <Liquiditypoolcard key={farm.liquidityid} liquidity={farm} account={account} removed={false} />
            ))}
           </FlexLayout>
         :         <FlexLayout>
            {farmsStakedMemoized.map((farm) => (
              <Liquiditypoolcard key={farm.liquidityid} liquidity={farm} account={account} removed />
            ))}
         </FlexLayout>
         }

      </div>
    )
  }

  const handleSortOptionChange = (option: OptionProps): void => {
    setSortOption(option.value)
  }

  return (
    <>
      <FarmsPageHeader>
        <Heading as="h1" scale="xxl" color="secondary" mb="24px">
          {t('Liquidity Pools')}
        </Heading>
        <Heading scale="lg" color="text">
          {t('Stake lp tokens to earn new tokens')}
        </Heading>
      </FarmsPageHeader>
      <Page>
        <ControlContainer>
          <ViewControls>
            <ToggleView viewMode={viewMode} onToggle={(mode: ViewMode) => setViewMode(mode)} />
            <ToggleWrapper>
              <Toggle checked={stakedOnly} onChange={() => setStakedOnly(!stakedOnly)} scale="sm" />
              <Text> {t('Staked only')}</Text>
            </ToggleWrapper>
            <LiquiditypoolTab showHistory={showHistory} setShowHistory={setShowHistory} hasStakeInFinishedFarms={stakedInactiveFarms.length > 0} />
          </ViewControls>
          <FilterContainer>
            <LabelWrapper>
              <Text>{t("Sort by")}</Text>
              <Select
                options={[
                  {
                    label: t('Hot'),
                    value: 'hot',
                  },
                  {
                    label: t('APR'),
                    value: 'apr',
                  },
                  {
                    label: t('Earned'),
                    value: 'earned',
                  },
                  {
                    label: t('Liquidity'),
                    value: 'liquidity',
                  },
                ]}
                onChange={handleSortOptionChange}
              />
            </LabelWrapper>
            <LabelWrapper style={{ marginLeft: 16 }}>
              <Text>{t("Search")}</Text>
              <SearchInput onChange={handleChangeQuery} />
            </LabelWrapper>
          </FilterContainer>
        </ControlContainer>
        {renderContent()}
        <div ref={loadMoreRef} />
      </Page>
    </>
  )
}
 
export default Liquiditypools