import getSyruppools from 'sdk/syruppools'
import syruppoolABI from 'sdk/abis/syruppool.json'
import erc20ABI from 'sdk/abis/erc20.json'
import multicall from 'utils/multicall'
import BigNumber from 'bignumber.js'

export const fetchPoolsAllowance = async (account) => {
  const calls = getSyruppools().map((p) => ({
    address: p.stakingToken.address,

    name: 'allowance',
    
    params: [account, p.contractAddress],
  }))

  const allowances = await multicall(erc20ABI, calls)

  return getSyruppools().reduce(
    (acc, pool, index) => ({ ...acc, [pool.syrupId]: new BigNumber(allowances[index]).toJSON() }),
    {},
  )
}

export const fetchUserBalances = async (account) => {
  // Non BNB pools
  const calls = getSyruppools().map((p) => ({
    address: p.stakingToken.address,

    name: 'balanceOf',
    
    params: [account],
  }))

  const tokenBalancesRaw = await multicall(erc20ABI, calls)

  const tokenBalances = getSyruppools().reduce(
    (acc, pool, index) => ({ ...acc, [pool.syrupId]: new BigNumber(tokenBalancesRaw[index]).toJSON() }),
    {},
  )

  return { ...tokenBalances }
}

export const fetchUserStakeBalances = async (account) => {
  const calls = getSyruppools().map((p) => ({
    address: p.contractAddress,

    name: 'userInfo',
    
    params: [account],
  }))

  const userInfo = await multicall(syruppoolABI, calls)

  const stakedBalances = getSyruppools().reduce(
    (acc, pool, index) => ({
      ...acc,
      [pool.syrupId]: new BigNumber(userInfo[index].amount._hex).toJSON(),
    }),
    {},
  )

  return { ...stakedBalances }
}

export const fetchUserPendingRewards = async (account) => {
  const calls = getSyruppools().map((p) => ({
    address: p.contractAddress,

    name: 'pendingReward',

    params: [account],
  }))

  const res = await multicall(syruppoolABI, calls)

  const pendingRewards = getSyruppools().reduce(
    (acc, pool, index) => ({
      ...acc,
      [pool.syrupId]: new BigNumber(res[index]).toJSON(),
    }),
    {},
  )

  return { ...pendingRewards }
}
