import React, { useState } from 'react'
import BigNumber from 'bignumber.js'
import { Button, Flex, Heading } from 'uikit'
import { Token } from 'sdk'
import { useTranslation } from 'contexts/Localization'
import useLiquidityHarvest from 'hooks/useLiquidityHarvest'
import { getBalanceNumber } from 'utils/formatBalance'
import { useWeb3React } from '@web3-react/core'
import useToast from 'hooks/useToast'

interface HarvestActionsProps {
  earnings?: BigNumber

  earningToken: Token

  liquidityid?: number
}

const HarvestAction: React.FC<HarvestActionsProps> = ({ earnings, earningToken, liquidityid }) => {
  const { account } = useWeb3React()

  const { t } = useTranslation()
  
  const [pendingTx, setPendingTx] = useState(false)

  const { toastSuccess } = useToast()

  const harvestCallback = ()=>
  {
    toastSuccess(`${t('Harvested')}!`, t(`Your earnings have been sent to your wallet!`))

    setPendingTx(false)
  }
  
  const [ onReward ] = useLiquidityHarvest(liquidityid, harvestCallback)

  const rawEarningsBalance = account ? getBalanceNumber(earnings, earningToken.decimals) : 0

  const displayBalance = rawEarningsBalance.toLocaleString()
    
  return (
    <Flex mb="8px" justifyContent="space-between" alignItems="center">
      <Heading color={rawEarningsBalance === 0 ? 'textDisabled' : 'text'}>
        {displayBalance}
      </Heading>
      <Button
        disabled={rawEarningsBalance === 0 || pendingTx}
        onClick={async () => {
          setPendingTx(true)
          await onReward()
        }}
      >
        {t('Harvest')}
      </Button>
    </Flex>
  )
}

export default HarvestAction
