import { useCallback } from 'react'
import { useWeb3React } from '@web3-react/core'
import { BIG_TEN } from 'sdk'
import BigNumber from 'bignumber.js'
import { useAppDispatch } from 'state'
import { fetchFarmUserDataAsync } from 'state/actions'
import { useLiquidityPoolContract } from './useContract'
import { calculateGasMargin } from '../utils'

export default function useLiquidityUnstake(
  liquidityId, enableEmergencyWithdraw = false, callback: any
): [ (amount: string, decimals: number) => Promise<void>] {
  const { account } = useWeb3React()

  const dispatch = useAppDispatch()

  const liquidityPoolContract = useLiquidityPoolContract(liquidityId)
  
  const onUnstake = useCallback(async (amount: string, decimals = 18): Promise<void> => {
    if (!liquidityPoolContract) {
      console.error('liquidity pool contract is null')

      return
    }

    if (enableEmergencyWithdraw) {
      const estimatedGas_em = await liquidityPoolContract.estimateGas.emergencyWithdraw()
                                                      
      // eslint-disable-next-line consistent-return
      liquidityPoolContract
             .emergencyWithdraw({ gasLimit: calculateGasMargin(estimatedGas_em) })
             .then(() => {
                callback()
  
                dispatch(fetchFarmUserDataAsync(account))
        })
        .catch((error: Error) => {
          console.error('Failed to approve token', error)
          throw error
        })
    } 
    else {
      const estimatedGas = await liquidityPoolContract.estimateGas.withdraw(new BigNumber(amount).times(BIG_TEN.pow(decimals)).toString())
                                                      
      // eslint-disable-next-line consistent-return
      liquidityPoolContract
             .withdraw((new BigNumber(amount).times(BIG_TEN.pow(decimals)).toString()), { gasLimit: calculateGasMargin(estimatedGas) })
             .then(() => {  
                dispatch(fetchFarmUserDataAsync(account))

                callback()
        })
        .catch((error: Error) => {
          console.error('Failed to unstake token', error)
          throw error
        })
    }
    
  }, [liquidityPoolContract, dispatch, account, callback, enableEmergencyWithdraw])

  return [ onUnstake ]
}
