import { useCallback } from 'react'
import { useAppDispatch } from 'state'
import { useWeb3React } from '@web3-react/core'
import { fetchFarmUserDataAsync } from 'state/actions'
import { useLiquidityPoolContract } from './useContract'
import { calculateGasMargin } from '../utils'

export default function useLiquidityHarvest(
  liquidityId, callback: any
): [ () => Promise<void>] {
  const { account } = useWeb3React()

  const dispatch = useAppDispatch()

  const liquidityPoolContract = useLiquidityPoolContract(liquidityId)
  
  const onReward = useCallback(async (): Promise<void> => {
    if (!liquidityPoolContract) {
      console.error('liquidity pool contract is null')

      return
    }

    const estimatedGas = await liquidityPoolContract.estimateGas.getReward()
                                                      
    // eslint-disable-next-line consistent-return
    liquidityPoolContract
           .getReward({ gasLimit: calculateGasMargin(estimatedGas) })
           .then(() => {
              dispatch(fetchFarmUserDataAsync(account))

              callback()
      })
      .catch((error: Error) => {
        console.error('Failed to harvest token', error)
        throw error
      })
    
  }, [liquidityPoolContract, dispatch, account, callback])

  return [ onReward ]
}

 
