import React, { useState, useRef, useEffect } from 'react'
import { Button, Skeleton } from 'uikit'
import BigNumber from 'bignumber.js'
import { getBalanceNumber } from 'utils/formatBalance'
import useLiquidityHarvest from 'hooks/useLiquidityHarvest'
import { useTranslation } from 'contexts/Localization'
import { useCountUp } from 'react-countup'
import useToast from 'hooks/useToast'
import { LiquidityPoolWithStakedValue } from '../../Liquiditypoolcard/types'
import { ActionContainer, ActionTitles, Title, Subtle, ActionContent, Earned, Staked } from './styles'

interface HarvestActionProps extends LiquidityPoolWithStakedValue {
  userDataReady: boolean
}

const HarvestAction: React.FunctionComponent<HarvestActionProps> = ({ liquidityid, earningToken, userData, userDataReady }) => {
  const earningsBigNumber = new BigNumber(userData.earnings)

  let earnings = 0

  const earningsBusd = 0

  let displayBalance = userDataReady ? earnings.toLocaleString() : <Skeleton width={60} />

  // If user didn't connect wallet default balance will be 0
  if (!earningsBigNumber.isZero()) {
    earnings = getBalanceNumber(earningsBigNumber, earningToken.decimals)
 
    displayBalance = earnings.toLocaleString()
  }

  const { toastSuccess } = useToast()

  const [pendingTx, setPendingTx] = useState(false)

  const harvestCallback = ()=>
  {
    toastSuccess(`${t('Harvested')}!`, t(`Your earnings have been sent to your wallet!`))

    setPendingTx(false)
  }

  const [ onReward ] = useLiquidityHarvest(liquidityid, harvestCallback)

  const { t } = useTranslation()

  const { countUp, update } = useCountUp({
    start: 0,
    end: earningsBusd,
    duration: 1,
    separator: ',',
    decimals: 3,
  })

  const updateValue = useRef(update)

  useEffect(() => {
    updateValue.current(earningsBusd)
  }, [earningsBusd, updateValue])

  return (
    <ActionContainer>
      <ActionTitles>
        <Title>{earningToken.symbol.toUpperCase()} </Title>
        <Subtle>{t('Earned')}</Subtle>
      </ActionTitles>
      <ActionContent>
        <div>
          <Earned>{displayBalance}</Earned>
          {countUp > 0 && <Staked>~{countUp}USD</Staked>}
        </div>
        <Button
          disabled={!earnings || pendingTx || !userDataReady}
          onClick={async () => {
            setPendingTx(true)
            await onReward()
          }}
          ml="4px"
        >
          {t('Harvest')}
        </Button>
      </ActionContent>
    </ActionContainer>
  )
}

export default HarvestAction
