import { useCallback, useMemo } from 'react'
import BigNumber from 'bignumber.js'
import { MaxUint256 } from '@ethersproject/constants'
import { useWeb3React } from '@web3-react/core'
import {  Liquidity } from 'sdk'
import { getLiquidityPoolContractAddress } from 'utils/addressHelpers'
import { useAppDispatch } from 'state'
import { fetchFarmUserDataAsync } from 'state/actions'
import { calculateGasMargin } from '../utils'
import { ApprovalState } from './useApproveCallback'
import { useTokenContract } from './useContract'
import { useSingleCallResult } from '../state/multicall/hooks'

export default function useLiquidityApprove(
  liquidity: Liquidity, liquidityId, callback: any
): [ApprovalState, () => Promise<void>] {
  const { account } = useWeb3React()

  const dispatch = useAppDispatch()
  
  const liquidityPoolContract = getLiquidityPoolContractAddress(liquidityId)

  const liquidityContract = useTokenContract(liquidity.address)

  const inputs = useMemo(() => [account, liquidityPoolContract], [account, liquidityPoolContract])

  const allowance = useSingleCallResult(liquidityContract, 'allowance', inputs).result

  const [ currentallowance ] = useMemo(() => [allowance ? new BigNumber(allowance.toString()) : undefined], [allowance])

  // check the current approval status
  const approvalState: ApprovalState = useMemo(() => {
    if (!liquidity || !liquidityPoolContract) return ApprovalState.UNKNOWN

    if (!currentallowance) return ApprovalState.UNKNOWN

    // amountToApprove will be defined if currentAllowance is
    return currentallowance.gt(0) ? ApprovalState.APPROVED : ApprovalState.NOT_APPROVED
  }, [liquidity, liquidityPoolContract, currentallowance])

  const approve = useCallback(async (): Promise<void> => {
    if (approvalState !== ApprovalState.NOT_APPROVED) {
      console.error('approve was called unnecessarily')

      return
    }

    if (!liquidityContract) {
      console.error('liquidityContract is null')

      return
    }

    if (!liquidityPoolContract) {
      console.error('no spender')

      return
    }

    const estimatedGas = await liquidityContract.estimateGas.approve(liquidityPoolContract, MaxUint256)

    // eslint-disable-next-line consistent-return
    liquidityContract
           .approve(liquidityPoolContract, MaxUint256, { gasLimit: calculateGasMargin(estimatedGas) })
           .then(() => {
            dispatch(fetchFarmUserDataAsync(account))

            callback()
      })
      .catch((error: Error) => {
        console.error('Failed to approve token', error)
        throw error
      })
  }, [approvalState, liquidityContract, liquidityPoolContract, dispatch, account, callback])

  return [approvalState, approve]
}

 
 
