import React from 'react'
import { useTranslation } from 'contexts/Localization'
import styled from 'styled-components'
import { Text, Flex, LinkExternal, TimerIcon, Skeleton } from 'uikit'
import { useBlock } from 'state/hooks'
import Balance from 'components/Balance'
import { LiquidityPoolWithStakedValue } from './types'

export interface ExpandableSectionProps {
  infoAddress?: string
  removed?: boolean
  totalValueFormatted?: string
  lpLabel?: string
  addLiquidityUrl?: string
  liquidity : LiquidityPoolWithStakedValue
}

const Wrapper = styled.div`
  margin-top: 24px;
`

const StyledLinkExternal = styled(LinkExternal)`
  font-weight: 400;
`

const DetailsSection: React.FC<ExpandableSectionProps> = ({
  removed,
  totalValueFormatted,
  lpLabel,
  addLiquidityUrl,
  liquidity
}) => {
  const { t } = useTranslation()

  const { currentBlock } = useBlock()

  const { startBlock, endBlock } = liquidity

  const shouldShowBlockCountdown = (startBlock > 0)

  const blocksUntilStart = Math.max(startBlock - currentBlock, 0)

  const blocksRemaining = Math.max(endBlock - currentBlock, 0)

  const hasPoolStarted = blocksUntilStart === 0 && blocksRemaining > 0

  return (
    <Wrapper>
      <Flex justifyContent="space-between">
        <Text>{t('Total Liquidity')}:</Text>
        <Text>{totalValueFormatted}</Text>
      </Flex>
      {shouldShowBlockCountdown && (
        <Flex mb="2px" justifyContent="space-between" alignItems="center">
          <Text small>{hasPoolStarted ? t('End') : t('Start')}:</Text>
          <Flex alignItems="center">
            {blocksRemaining || blocksUntilStart ? (
              <Balance
                color="primary"
                fontSize="14px"
                value={hasPoolStarted ? blocksRemaining : blocksUntilStart}
                decimals={0}
              />
            ) : (
              <Skeleton width="54px" height="21px" />
            )}
            <Text ml="4px" color="primary" small>
              {t('blocks')}
            </Text>
            <TimerIcon ml="4px" color="primary" />
          </Flex>
        </Flex>
      )}
      {!removed && (
        <StyledLinkExternal href={addLiquidityUrl}>{t("getLP", { name: lpLabel })}</StyledLinkExternal>
      )}
    </Wrapper>
  )
}

export default DetailsSection
