import React from "react";
import { useTranslation } from 'contexts/Localization'
import { useModal } from "../Modal";
import ChangeChainModal from "./ChangeChainModal";
import { ChangeNetwork } from './types'

interface ReturnType 
{
  onPresentChangeChainModal: () => void;
}

const useChangeChainModal = (onChangeNetwork: ChangeNetwork): ReturnType => 
{
  const { t } = useTranslation()

  const [onPresentChangeChainModal] = useModal(<ChangeChainModal t={t} onChangeNetwork={onChangeNetwork}/>);
  
  return { onPresentChangeChainModal };
};

export default useChangeChainModal;
