import React, { useState, useCallback } from 'react'
import styled from 'styled-components'
import { Button, useModal, IconButton, AddIcon, MinusIcon, Skeleton } from 'uikit'
import { useLocation } from 'react-router-dom'
import UnlockButton from 'components/UnlockButton'
import { useWeb3React } from '@web3-react/core'
import { useFarmUser } from 'state/hooks'
import { useTranslation } from 'contexts/Localization'
import useLiquidityApprove from 'hooks/useLiquidityApprove'
import getLiquidityUrlPathParts from 'utils/getLiquidityUrlPathParts'
import { getBalanceNumber, getFullDisplayBalance } from 'utils/formatBalance'
import useToast from 'hooks/useToast'
import { ApprovalState } from 'hooks/useApproveCallback'
import useLiquidityStake from 'hooks/useLiquidityStake'
import useLiquidityUnstake from 'hooks/useLiquidityUnstake'
import { LiquidityPoolWithStakedValue } from '../../Liquiditypoolcard/types'
import DepositModal from '../../DepositModal'
import WithdrawModal from '../../WithdrawModal'
import { ActionContainer, ActionTitles, ActionContent, Earned, Title, Subtle } from './styles'

const IconButtonWrapper = styled.div`
  display: flex;
`

interface StackedActionProps extends LiquidityPoolWithStakedValue {
  userDataReady: boolean
}

const Staked: React.FunctionComponent<StackedActionProps> = ({
  liquidityid,
  liquidity,
  userDataReady,
}) => {
  const { t } = useTranslation()

  const { account } = useWeb3React()

  const [requestedApproval, setRequestedApproval] = useState(false)

  const { allowance, tokenBalance, stakedBalance } = useFarmUser(liquidityid)

  const { toastSuccess, toastError } = useToast()

  let stakeCallbackHander

  const stakeModalCallbackSetting = ((callback)=>
  {
    stakeCallbackHander = callback
  })

  const stakeCallback = () =>
  {
    if(stakeCallbackHander)
    {
      stakeCallbackHander()
    }
  }

  const [ onStake ] = useLiquidityStake(liquidityid, stakeCallback)

  let unstakeCallbackHander

  const unstakeModalCallbackSetting = ((callback)=>
  {
    unstakeCallbackHander = callback
  })

  const unstakeCallback = () =>
  {
     if(unstakeCallbackHander)
     {
       unstakeCallbackHander()
     }
  }

  const [ onUnstake ] = useLiquidityUnstake(liquidityid, false, unstakeCallback)

  const location = useLocation()

  const isApproved = account && allowance && allowance.isGreaterThan(0)

  const liquidityUrlPathParts = getLiquidityUrlPathParts({
    quoteTokenAddress: liquidity.quoteToken.address,

    tokenAddress: liquidity.token.address,
  })
  const addLiquidityUrl = `#/add/${liquidityUrlPathParts}`

  const displayBalance = useCallback(() => {
    const stakedBalanceNumber = getBalanceNumber(stakedBalance)

    if (stakedBalanceNumber > 0 && stakedBalanceNumber < 0.0001) {
      return getFullDisplayBalance(stakedBalance).toLocaleString()
    }

    return stakedBalanceNumber.toLocaleString()
  }, [stakedBalance])

  const [onPresentDeposit] = useModal(
    <DepositModal max={tokenBalance} onConfirm={onStake} tokenName={liquidity.symbol} decimals={18} setCallback={stakeModalCallbackSetting} addLiquidityUrl={addLiquidityUrl} />,
  )
  const [onPresentWithdraw] = useModal(<WithdrawModal max={stakedBalance} onConfirm={onUnstake} decimals={18} tokenName={liquidity.symbol} setCallback={unstakeModalCallbackSetting} />)

  const approveCallback = () =>
  {
    toastSuccess(`${t('Contract Enabled')}`, `${t(`You can now stake in the farm!`)}`)
       
    setRequestedApproval(false)
  }

  const [ approveState, onApprove ] = useLiquidityApprove(liquidity, liquidityid, approveCallback)

  const handleApprove = useCallback(async () => {
    try {
      setRequestedApproval(true)

      await onApprove()

      if (approveState ===  ApprovalState.UNKNOWN) {
        // user rejected tx or didn't go thru
        toastError(
          `${t('Error')}`,
          `${t(`Please try again. Confirm the transaction and make sure you are paying enough gas!`)}`,
        )

        setRequestedApproval(false)
      }  else if (approveState === ApprovalState.APPROVED)
      {
        toastSuccess(`${t('Contract Enabled')}`, `${t(`You can now stake in the farm!`)}`)
       
        setRequestedApproval(false)
      }

    } catch (e) {
      console.error(e)
    }
  }, [onApprove, approveState, toastSuccess, toastError, t])

  if (!account) {
    return (
      <ActionContainer>
        <ActionTitles>
          <Subtle>{t('Start Farming')}</Subtle>
        </ActionTitles>
        <ActionContent>
          <UnlockButton width="100%" />
        </ActionContent>
      </ActionContainer>
    )
  }

  if (isApproved) {
    if (stakedBalance.gt(0)) {
      return (
        <ActionContainer>
          <ActionTitles>
            <Title>{liquidity.symbol} </Title>
            <Subtle>{t('Staked')}</Subtle>
          </ActionTitles>
          <ActionContent>
            <div>
              <Earned>{displayBalance()}</Earned>
            </div>
            <IconButtonWrapper>
              <IconButton variant="secondary" onClick={onPresentWithdraw} mr="6px">
                <MinusIcon color="primary" width="14px" />
              </IconButton>
              <IconButton
                variant="secondary"
                onClick={onPresentDeposit}
                disabled={['history', 'archived'].some((item) => location.pathname.includes(item))}
              >
                <AddIcon color="primary" width="14px" />
              </IconButton>
            </IconButtonWrapper>
          </ActionContent>
        </ActionContainer>
      )
    }

    return (
      <ActionContainer>
        <ActionTitles>
          <Subtle>{t('Stake')} </Subtle>
          <Title>{liquidity.symbol}</Title>
        </ActionTitles>
        <ActionContent>
          <Button
            width="100%"
            onClick={onPresentDeposit}
            variant="secondary"
            disabled={['history', 'archived'].some((item) => location.pathname.includes(item))}
          >
            {t('Stake LP')}
          </Button>
        </ActionContent>
      </ActionContainer>
    )
  }

  if (!userDataReady) {
    return (
      <ActionContainer>
        <ActionTitles>
          <Subtle>{t('Start Farming')}</Subtle>
        </ActionTitles>
        <ActionContent>
          <Skeleton width={180} marginBottom={28} marginTop={14} />
        </ActionContent>
      </ActionContainer>
    )
  }

  return (
    <ActionContainer>
      <ActionTitles>
        <Subtle>{t('ENABLE FARM')}</Subtle>
      </ActionTitles>
      <ActionContent>
        <Button width="100%" disabled={requestedApproval} onClick={handleApprove} variant="secondary">
          {t('Enable')}
        </Button>
      </ActionContent>
    </ActionContainer>
  )
}

export default Staked
